let url = {
  'banner': 'data:image/png;base64,',
  'category': 'data:image/png;base64,',
  'product_logo': 'data:image/png;base64,',
  'product_banner': 'data:image/png;base64,',
  'ad': 'data:image/png;base64,'
}
export default {
  'banner': url.banner,
  'category': url.category,
  'product_logo': url.product_logo,
  'product_banner': url.product_banner,
  'ad': url.ad
}



